import babelpolyfill from 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import $ from 'jquery'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import VueRouter from 'vue-router'
import store from './vuex/store'
import Vuex from 'vuex'
import NProgress from 'nprogress'//
import 'nprogress/nprogress.css'

import Login from './components/Login.vue'
import Home from './components/Home.vue'
import Main from './components/Main.vue'

//import businessCount from './components/nav1/businessCount.vue'

//
import BusinessCount from './components/statistic-analysis/BusinessCount.vue' //
import AutoValuationCount from './components/statistic-analysis/AutoValuationCount.vue' //
import OrgCostCount from './components/statistic-analysis/OrgCostCount.vue' //
import BuildingKeywordsCount from './components/statistic-analysis/BuildingKeywordsCount.vue' //
//
import DataExport from './components/data-management/DataExport.vue' //
import DataImport from './components/data-management/DataImport.vue' //
import DataPremission from './components/data-management/DataPremission.vue' //
//
import RoleManagement from './components/sys-menagement/RoleManagement.vue' //
import UserManagement from './components/sys-menagement/UserManagement.vue' //



Vue.use(ElementUI)
Vue.use(VueRouter)
Vue.use(Vuex)

const routes = [
  {
    path: '/login',
    component: Login,
    name: '',
    hidden: true//
  },
  //{ path: '/main', component: Main },
  {
    path: '/',
    component: Home,
    redirect: '/login',
    name: '',
    iconCls: 'fa fa-bar-chart',//class
    children: [
      //{ path: '/main', component: Main },
      { path: '/BusinessCount', component: BusinessCount, name: '' },
  	  { path: '/AutoValuationCount', component: AutoValuationCount, name: '' },
  	  { path: '/OrgCostCount', component: OrgCostCount, name: '' },
  	  { path: '/BuildingKeywordsCount', component: BuildingKeywordsCount, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-database',
    children: [
      { path: '/DataExport', component: DataExport, name: '' },
      { path: '/DataImport', component: DataImport, name: '' },
      { path: '/DataPremission', component: DataPremission, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'el-icon-setting',
    children: [
      { path: '/RoleManagement', component: RoleManagement, name: '' },
      { path: '/UserManagement', component: UserManagement, name: '' }
    ]
  }
]

let permissionData = $.parseJSON(localStorage.getItem("permission"));
if(permissionData != null && permissionData != undefined && permissionData.length > 0){
  $.each(routes, function(index, el) {
    if(permissionData.includes(el.name)){
      el.hidden = false;
    }else{
      el.hidden = true;
    }
    $.each(el.children,function(i, v) {
      if(permissionData.includes(v.name)){
        el.hidden = false;
        v.hidden = false;
      }else{
        v.hidden = true;
      }
    });
  });
}

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  NProgress.start();
  next()
  //console.log(to);
})

router.afterEach(transition => {
  NProgress.done();
});

new Vue({
  el: '#app',
  template: '<App/>',
  router,
  store,
  components: { App }
  //render: h => h(Login)
}).$mount('#app')

//router.replace('/login')




