<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" class="demo-form-inline">
				<el-form-item label="" label-width="90px">
				    <el-select placeholder="" v-model="searchData.type">
				      <el-option label="VQ API" value="all"></el-option>
				      <el-option label="VQ API" value="c1"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="" label-width="60px">
				    <el-select placeholder=""  v-model="searchData.company">
				      <el-option label="" value="all"></el-option>
				      <el-option label="" value="c1"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="" label-width="110px">
				    <el-date-picker v-model="searchData.dataRange" type="datetimerange" :picker-options="pickerOptions" placeholder="" align="right">
    				</el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="tableData" fit highlight-current-row v-loading="listLoading" style="width: 100%;" border>
				<el-table-column prop="businessType" label="" width="160"></el-table-column>
				<el-table-column prop="company" label="" width="140"></el-table-column>
				<el-table-column prop="dataRecordTime" label="" width="360"></el-table-column>
				<el-table-column prop="exportTime" label="" width="180"></el-table-column>
				<el-table-column prop="dataCount" label="" width="80"></el-table-column>
				<el-table-column prop="status" label="" :formatter="formatStatus"></el-table-column>
				<el-table-column prop="author" label="" width="120" :formatter="formatOnline"></el-table-column>
				<el-table-column inline-template :context="_self" width="80" fixed="right" label="">
					<span>
						<el-button type="text" size="small"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px; padding-top: 15px">
			<el-pagination @current-change="handleCurrentChange" :current-page="1":page-size="pagerData.pageSize" layout="total, prev, pager, next, jumper":total="pagerData.total" style="float:right">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
	created:function () {
	    //this.getData();
    },
    data() {
        return {
        	searchData : {
        		type : '',
        		company : '',
        		dataRange : ''
        	},
			tableData: [{
				businessType : 'VQ API',
				company : '',
				dataRecordTime : '2016-12-19 00:00:00  2016-12-20 23:59:59',
				exportTime : '2016-12-22  10:10:10',
				dataCount : '100',
				status : '',
				author : 'Bruce'
			},{
				businessType : 'VQ API',
				company : '',
				dataRecordTime : '2016-12-19 00:00:00  2016-12-20 23:59:59',
				exportTime : '2016-12-22  10:10:10',
				dataCount : '100',
				status : '',
				author : 'Bruce'
			},{
				businessType : 'VQ API',
				company : '',
				dataRecordTime : '2016-12-19 00:00:00  2016-12-20 23:59:59',
				exportTime : '2016-12-22  10:10:10',
				dataCount : '100',
				status : '',
				author : 'Bruce'
			}],
			pagerData : {
				total : 50,
				pageIndex : 1,
				pageSize : 10
			},
			pickerOptions: {
		        shortcuts: [{
		            text: '',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
		              picker.$emit('pick', [start, end]);
		            }
		        }, {
		            text: '',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
		              picker.$emit('pick', [start, end]);
		            }
		        }, {
		            text: '',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
		              picker.$emit('pick', [start, end]);
		            }
		        }]
		    },
		}
    },
    methods: {
        handleCurrentChange(val) {
	        this.pagerData.pageIndex = val;
	        //this.getData();
        }
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}
	.toolbar {
		background: #fff;
	}
	
</style>




