export default {
    /**/
    checkLogin: function(code,el,callback){
        let isOnline = localStorage.getItem("isOnline");
        if(code && code == -1){
            $(".el-notification").remove();
            el.$notify({title: '',message: "", type: 'error',duration: 2000});
            setTimeout(function(){
                el.$router.replace('/login');
            },2000);
        }else if(isOnline && isOnline == 0){
            $(".el-notification").remove();
            el.$notify({title: '',message: "", type: 'error',duration: 2000});
        }else{
            callback();
        }
    },
    /*token*/
    getToken: function(){
        return localStorage.getItem("token");
    },
    /*iso8601*/
    formatTime: function(el){
        return el != null ? el.toLocaleDateString().replace(/\//g, "-") + " " + el.toTimeString().substr(0, 8) : '';
    }
};



